@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

body {
    font-family: 'Poppins', sans-serif;
    background-color: #D9F5FF;
    background-repeat: no-repeat;
}

.App {
    width: 60%;
    margin: 0 auto;
    text-align: center;
    background-image: linear-gradient(130deg, #0c9cd4, #b367d6);
    box-shadow: rgba(11, 173, 6, 0.68) 0px 0px 1rem 2px;
    border-radius: 10px;
    margin-top: 3%;
    padding-bottom: 5%;
}

.header {
    padding: 2% 0;
    font-size: 19px;
    color: #fff;
}

.header h2 {
    text-transform: uppercase;
    font-weight: 800;
}

.form-add-money input[type="number"] {
    font-size: inherit;
    padding: 7px 17px;
    border: 0px;
    border-radius: 5px;
    outline: none;
}

.form-add-money input[type="submit"] {
    display: block;
    margin: 21px auto 0px;
    font-size: inherit;
    padding: 8px 7px;
    width: 230px;
    border: 0px;
    border-radius: 5px;
    cursor: pointer;
    background-color: #060708;
    color: #fff;
}

.form-add-money p {
    color: #fff;
    font-weight: 500;
}

p.error {
    color: #fff;
    background-color: #d52121;
    display: inline-block;
    padding: 1px 18px 4px;
    border-radius: 7px;
    font-weight: 400;
    display: block;
}

.main-form {
    display: flex;
    justify-content: center;
    gap: 40px;
    margin-bottom: 6%;
}

.balance {
    text-align: left;
    background-color: rgb(250 250 250 / 68%);
    border-radius: 13px 47px;
    padding: 12px 15px;
    display: grid;
    place-content: center;
}

.add-subscription form p {
    font-weight: 400;
    color: #fff;
    margin-bottom: 4px;
}

.add-subscription h3 {
    margin-top: 0;
}

.add-subscription form select {
    font-size: inherit;
    padding: 7px 7px;
    border: 0px;
    border-radius: 5px;
    width: 230px;
    text-align: center;
    outline: none;
}

.add-subscription input[type="number"] {
    font-size: inherit;
    padding: 8px 9px;
    width: 177px;
    border: 0px;
    outline: none;
    border-radius: 4px;
}

.add-subscription input[type="submit"] {
    display: block;
    margin: 21px auto 0px;
    font-size: inherit;
    padding: 8px 7px;
    width: 196px;
    border: 0px;
    border-radius: 5px;
    cursor: pointer;
    background-color: #060708;
    color: #fff;
    transition-duration: .45s;
    font-weight: 600;
}

.add-subscription input[type="submit"]:hover {
    background-color: #1264b6;
    color: #fff;
}

.single-item {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #fff;
    margin-bottom: 3%;
    border-radius: 10px;
    height: 110px;
    margin: 2% 4% 2%;
}

.single-item a {
    text-decoration: none;
    font-weight: 600;
    background-color: aquamarine;
    padding: 5px 11px 8px;
    border-radius: 5px;
    color: #000;
}

.single-item a.delete:hover {
    background-color: #ff020282;
}

.single-item a.edit:hover {
    background-color: #2499f18c;
}

.single-item img {
    width: 155px;
    height: auto;
}

p.author {
    display: flex;
    justify-content: center;
    margin-top: 3%;
    gap: 7px;
    color: #000;
}

p.author a {
    text-decoration: none;
    font-weight: 700;
    color: #000;
}